@import '../../../styles/vars';
.categories{
  @include flex(20px, 0, 0, wrap);
  .item{
    box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.5);
    user-select: none;
    font-weight: 700;
    cursor: pointer;
    border-radius: 20px;
    padding: 10px 20px;
    color: $primaryColor;
    background: $bgGrad;
    &:hover{
      transform: scale(1.1);
      border-radius: 10px;
      background: $bgGreen;
      transition: all .15s linear;
    }
  }
  .active{
    color: $primaryColor;
    border-radius: 10px;
    background: $bgGreen;
  }
}
@media (max-width: 800px) {
  .categories{
    gap: 10px;
    .item{
      font-size: 0.8rem;
      padding: 10px 13px;
      &:nth-child(2){
        order: 2;
      }
    }
  }
}